import { createBrowserHistory, LocationState } from 'history';
import { composeWithDevTools } from 'redux-devtools-extension';
import { getThunkExtension } from 'redux-dynamic-modules-thunk';
import { createStore, IModule } from 'redux-dynamic-modules';
import { connectRouter, routerMiddleware } from 'connected-react-router';

import { BlogModuleState } from 'CounterFirst/redux/types';
import { counterSecondReducer } from './counterSecond/counterSecond';

// browser history
export const history = createBrowserHistory();

// rehydrate state on app start
const initialState = {};

const routerModule = (): IModule<LocationState> => ({
  id: 'router-module',
  reducerMap: {
    router: connectRouter(history),
  },
  middlewares: [routerMiddleware(history)],
});

const rootModule = () => ({
  id: 'root-module',
  reducerMap: {
    counterSecond: counterSecondReducer,
  },
});

const store = createStore(
  {
    initialState,
    extensions: [getThunkExtension()],
    advancedComposeEnhancers: composeWithDevTools({
      name: 'dynamic-modules-app',
    }),
  },
  rootModule(),
  routerModule(),
);

export type DynamicModules = BlogModuleState;

export type StaticModules = {
  counterSecond: ReturnType<typeof counterSecondReducer>;
};

export type RootState = DynamicModules & StaticModules;

export type AppDispatch = typeof store.dispatch;

export default store;

// https://github.com/microsoft/redux-dynamic-modules/issues/118
