import * as React from 'react';
import CounterFirstLoader from 'CounterFirst/components/CounterFirstLoader';
import CounterSecond from 'components/CounterSecond';

const App: React.FC = () => {
  const [isVisibleCounterFirst, setVisibleCounterFirst] = React.useState(false);

  const toggleVisible = () => setVisibleCounterFirst((s) => !s);

  return (
    <div>
      <button type="button" onClick={toggleVisible}>
        re
      </button>
      {isVisibleCounterFirst && <CounterFirstLoader />}
      <CounterSecond />
    </div>
  );
};

export default App;
